import React from 'react';
import Item from "./TodoItem"


const TodoList = function({todo,setState}){
	const click = function(e){
		const {currentTarget} = e;
		const idx = currentTarget.getAttribute("data-idx");
		const item = todo[idx];
		todo[idx].select = item.select ? false : true;
		setState([...todo]);
	}
	const remove = function(e){
		e.stopPropagation();
		const {target} = e;
		const parent = target.parentNode;
		const idx = parent.getAttribute("data-idx");
		todo = todo.filter( (item,fidx) => fidx != idx )
		setState([...todo]);
	}
	return (
		<ul>
			{ todo.map( ({text,select},idx) => <Item click={click} remove={remove} idx={idx} text={text} select={select}></Item> ) }
		</ul>
	);
}


export default TodoList;