import React from 'react';


const Form = function({todo,setState}){
	const keydown = function(e){
		const {target,keyCode} = e;
		const {value} = target;
		const result = [9,13,32].includes(keyCode);
		const trim = value.trim().length;
		if( result && trim ){
			e.preventDefault();
			setState([...todo,{
				text:value,
				select:false
			}]);
			target.value = "";
		}
	}
	return(
		<div className="form">
			<input type="text" onKeyDown={keydown} />
		</div>
	);
}

export default Form;
